import React, { Fragment, Children } from 'react'
import { omit } from 'ramda'

const PropsToChildren = ({...props}) => {
  const { addedProps, children } = props
  const cleanProps = omit(['addedProps', 'children'], props)
  return (
    <Fragment>
      {React.Children.map(children, (Child) => {
        return React.cloneElement(Child, { ...addedProps, ...cleanProps, ...Child.props })
      })}
    </Fragment>
  )
}

export default PropsToChildren
